.box-shares {
  padding-top: 57px;
  padding-bottom: 55px;
  margin: 0;
}

/* */
.box-shares h1 {
  margin-bottom: 35px;
}

.shares {
  overflow: hidden;
  min-height: 501px;
  padding-bottom: 44px;
  margin-bottom: 41px;
  position: relative;
  display: table;
  border-bottom: solid 1px #dcdcdc;
}

.shares:after {
  content: '';
  width: 100%;
  height: 1px;
  position: absolute;
  left: 0;
  bottom: 4px;
  display: block;
  background: #dcdcdc;
}

.shares:last-child {
  border: none;
  margin-bottom: 0;
}

.shares:last-child:after {
  display: none;
}

.shares-text {
  overflow: hidden;
  position: relative;
  display: table-cell;
  padding-left: 487px;
  padding-right: 52px;
  vertical-align: middle;
  font-size: 16px;
  color: #272425;
}

.shares-text span {
  font-family: "Amigo",sans-serif;
  font-weight: bold;
  font-size: 18px;
  color: #b49e5c;
  text-transform: uppercase;
  margin-right: 6px;
  display: inline-block;
}

.shares-text .shares-img {
  width: 457px;
  position: absolute;
  top: 0;
  left: 0;
}

.shares:nth-child(odd) .shares-text {
  padding-left: 0;
  padding-right: 543px;
}

.shares:nth-child(odd) .shares-img {
  left: auto;
  right: 0;
}

.icon-load {
  width: 120px;
  height: 120px;
  position: fixed;
  text-align: center;
  left: 50%;
  top: 50%;
  margin-left: -60px;
  margin-top: -60px;
  display: none;
}

.loaded .icon-load {
  display: block;
}

@media screen and (min-width: 768px) {
  .main-wrapper {
    visibility: visible;
    opacity: 1;
    transition: all 0.3s linear;
  }
  .loaded .main-wrapper {
    visibility: hidden;
    opacity: 0;
  }
}

@media screen and (max-width: 1138px) {
  .shares-text {
    padding-right: 0;
  }
  .shares:nth-child(odd) .shares-text {
    padding-right: 487px;
  }
}

@media screen and (max-width: 880px) {
  .shares {
    display: block;
    min-height: auto;
  }
  .shares-text {
    display: block;
    padding: 0;
  }
  .shares-text .shares-img {
    position: relative;
    display: block;
    margin: 0 auto 20px;
  }
  .shares:nth-child(odd) .shares-text {
    padding-left: 0;
    padding-right: 0;
  }
  .shares:nth-child(odd) .shares-img {
    left: auto;
    right: auto;
    position: relative;
  }
}

@media screen and (max-width: 488px) {
  .shares-text .shares-img {
    width: 100%;
  }
}

